.custome_switch{
	position: absolute;
	top: 10px;
	right: 0px;
}
.switch {
	position: relative;
	display: inline-block;
	width: 36px;
	height: 20px;
}
.switch input { 
	opacity: 0;
	width: 0;
	height: 0;
}
.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #bfc2c7;
	-webkit-transition: .4s;
	transition: .4s;
}
.slider:before {
	position: absolute;
	content: "";
	height: 14px;
	width: 14px;
	left: 4px;
	bottom: 3px;
	background-color: white;
	-webkit-transition: .4s;
	transition: .4s;
}
input:checked + .slider {
	background-color: #ffd316;
}
input:focus + .slider {
	box-shadow: 0 0 1px #ffd316;
}
input:checked + .slider:before {
	-webkit-transform: translateX(14px);
	transform: translateX(14px);
}
.slider.round {
	border-radius: 34px;
}
.slider.round:before {
	border-radius: 50%;
}
