.plans_checkbox_label {
	display: block;
	position: relative;
	margin-bottom: 12px;
	cursor: pointer;
	font-size: 22px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	background-color: #ffffff;
}
.plans_checkbox_label input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	height: 0;
	width: 0;
}
.plans_checkbox .checkmark {
	position: absolute;
	top: 35px;
	left: 15px;
	height: 25px;
	width: 25px;
	border: 1px solid #c9c9c9;
	border-radius: 3px;
}
.plans_checkbox_label:hover input ~ .checkmark {
	background-color: #ccc;
}
.plans_checkbox_label input:checked ~ .checkmark {
	background-color: #60c07a;
}
.plans_checkbox .checkmark:after {
	content: "";
	position: absolute;
	display: none;
}
.plans_checkbox_label input:checked ~ .checkmark:after {
	display: block;
}
.plans_checkbox_label .checkmark:after {
	left: 9px;
    top: 3px;
    width: 6px;
    height: 14px;
    border: solid #fff;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.plans_checkbox_label input:checked ~ .plans_box .plans_cancle{
	display: inline-block !important;
}
